import React from 'react';
import { DatePanel } from '../DatePanel';
import type { IDateTimePanelProps } from '../interface';
import { TimePanel } from '../TimePanel';
import { DateTimePanelWrapper } from './styled';

export const DateTimePanel: React.FC<IDateTimePanelProps> = React.memo((props) => {
  const { value, showTime, curPanel, onSelect, showNow, timeFormat, ...rest } = props

  return <DateTimePanelWrapper>
    <DatePanel curPanel={curPanel} value={value} onSelect={onSelect} showTime={showTime} showNow={showNow} {...rest} />
    {showTime && curPanel === 'date' ? <TimePanel value={value} onSelect={onSelect} showNow={showNow} timeFormat={timeFormat} /> : null}
  </DateTimePanelWrapper>;
});
